import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServiceProviderService } from '../core/service-provider.service';
import * as moment from 'moment';
import { UtilityService } from '../core/utility.service';
import { AppointmentService } from '../core/appointment.service';
@Component({
  selector: 'app-availability',
  templateUrl: './availability.page.html',
  styleUrls: ['./availability.page.scss'],
})
export class AvailabilityPage implements OnInit {
  data;
  date: string;
  type: 'string';
  selectedDate = moment().toString();
  slots;
  serviceProviderId;
  constructor(
    private activatedRoute: ActivatedRoute,
    private service: ServiceProviderService,
    private utility: UtilityService,
    private appointment: AppointmentService
  ) {
    console.log('working');
  }

  ngOnInit() {}
  ionViewWillEnter() {
    this.getData();
  }

  onChange($event) {
    console.log($event);
    let date = $event.toString();
    console.log(date);
    this.selectedDate = date;
    console.log(this.selectedDate);
    this.getSlotsData();
  }

  getData() {
    console.log('working');
    let id = this.activatedRoute.snapshot.queryParams.id;
    this.serviceProviderId = id;
    this.service.getSingleServiceProvider(id).subscribe((res) => {
      console.log(res);
      this.data = res;
      this.getSlotsData();
    });
  }

  getSlotsData() {
    this.utility.present();

    let id = this.activatedRoute.snapshot.queryParams.id;

    this.service.getSlots(id, this.selectedDate).subscribe((res) => {
      this.slots = res;
      console.log(this.slots, 'slots');

      this.utility.dismiss();
    });
  }
  timeBook(s) {
    console.log(s);
    let start = s.slot.split('-')[0];
    let end = s.slot.split('-')[1];
    start = parseInt(start.split(':')[0]);
    end = parseInt(end.split(':')[0]);

    console.log(start, end);
    let startDate = moment(this.selectedDate)
      .set({ hour: start, minute: 0, second: 0 })
      .toString();
    let endDate = moment(this.selectedDate)
      .set({ hour: end, minute: 0, second: 0 })
      .toString();
    console.log(startDate, endDate);
    console.log(moment(this.selectedDate).set({ hour: start }).toString());
  }

  addAppointment(slot) {
    this.utility.present();
    let obj = {
      serviceProvider: this.data.userId._id,
      user: this.utility.getUserId(),
      service: this.data.service,
      serviceName: this.data.sname,
      startTime: slot.split('-')[0],
      endTime: slot.split('-')[1],
      appointmentDate: this.selectedDate,
    };

    this.appointment.addAppointment(obj).subscribe(
      (res) => {
        console.log(res);
        if (res['status'] == true) {
          this.utility.presentToastWithOptions(
            'Appointment Booked Successfully.'
          );

          this.utility.navigate('/tabs/appointments');
        } else {
          this.utility.showAlert(res['message']);
        }

        this.getData();
        this.utility.dismiss();
      },
      (err) => {
        this.utility.dismiss();
      }
    );
  }

  addTime(slot) {
    console.log(slot, 'slot');
    this.addAppointment(slot);
  }
}
